import "./styles.css";

import cloudArrowDown from "../../images/CloudArrowDown.svg";
import envelopeSimple from "../../images/EnvelopeSimple.svg";
import handshake from "../../images/Handshake.svg";
import downloadSimple from "../../images/DownloadSimple.svg";

export const Info = () => {
  return (
    <section className="section info">
      <h4 className="info__header">Rozpoczcie wsppracy krok po kroku</h4>
      <div className="info__steps">
        <div className="info__tile">
          <img
            alt="download icon"
            className="info__tileImage"
            src={cloudArrowDown}
          />
          <h4 className="info__tileHeader">Wypenij wywiad</h4>
          <p className="info__tileParagraph">
            Wypenij arkusz z wywiadem ywieniowym, ktry pobierzesz
            bezporednio z tej strony klikajc poniszy przycisk.
          </p>
        </div>
        <div className="info__tile">
          <img
            alt="envelope"
            className="info__tileImage"
            src={envelopeSimple}
          />
          <h4 className="info__tileHeader">Wylij go do mnie</h4>
          <p className="info__tileParagraph">
            Wypeniony arkusz wylij wraz z nazw wybranego przez Ciebie pakietu
            dietetycznego na mail&nbsp;
            <a href="mailto:123@gmail.com">mail@mail.com</a> lub korzystajc
            z&nbsp;
            <a href="#contact">formularza</a>.
          </p>
        </div>
        <div className="info__tile">
          <img alt="handshake" className="info__tileImage" src={handshake} />
          <h4 className="info__tileHeader">Otrzymaj odpowied</h4>
          <p className="info__tileParagraph">
            Gotowe! Wanie rozpoczynam prac nad ofert dla Ciebie. W cigu 2
            dni otrzymasz odpowied mailow ze szczegami naszej wsppracy.
          </p>
        </div>
      </div>
      <button className="info__button">
        <a
          href="../../files/Wywiad-ywieniowy.pdf"
          download
          className="info__link--download"
        >
          <span className="info__buttonText">ARKUSZ Z WYWIADEM</span>
          <img
            alt="download"
            width="24px"
            className="info__buttonImage"
            src={downloadSimple}
          />
        </a>
      </button>
    </section>
  );
};
