import "./styles.css";
import image from "../../images/dietetyk_roku.jpeg";

export const About = () => {
  return (
    <section className="section about">
      <img
        style={{ gridArea: "picture" }}
        width="100%"
        src={image}
        alt=""
        className="about__image"
      />
      <article style={{ gridArea: "text" }} className="about__article">
        <h2 className="about__header">
          O mnie - czyli jak wybra dobrego dietetyka?
        </h2>
        <p className="about__paragraph">
          Nazywam si Patrycja Rzepczyska i jestem dietetykiem klinicznym.
          Ukoczyam studia na Uniwersytecie Przyrodniczym we Wrocawiu na
          kierunku ywienie czowieka i dietetyka oraz wiele kursw z zakresu
          dietetyki klinicznej, jak i sportowej. W wolnym czasie tworz ciekawe
          przepisy, ktre znajdziesz na&nbsp;
          <a
            href="https://www.instagram.com/diet_the_difference/"
            target="_blank"
            rel="noreferrer"
          >
            moim instagramie
          </a>
          .
        </p>
      </article>
    </section>
  );
};
